import React, { useState } from 'react'
import { ReactComponent as Soundcloud } from '../images/soundcloud.svg'
import { ReactComponent as Spotify } from '../images/spotify-logo.svg'
import { ReactComponent as Instagram } from '../images/instagram.svg'
import './Socials.css'


const Socials = () => {

    const [over, setOver] = useState(false);
    let buttonstyle = {
        fill: ''
    }
    if (over) {
        buttonstyle.fill = "green";
    }
    else {
        buttonstyle.fill = '';
    }


    return (
        <div className='socials'>
            <a href='https://www.instagram.com/darealstyl/'>
                <button
                    style={buttonstyle}
                    onMouseOver={() => setOver(true)}
                    onMouseOut={() => setOver(false)}
                    type="button"
                    className="instagram"
                    aria-label="InstagramLogo"
                >
                    <Instagram />
                </button>
            </a>
            <a href='https://soundcloud.com/darealstyl'>
                <button
                    type="button"
                    className="soundcloud"
                    aria-label="SoundcloudLogo"
                >
                    <Soundcloud />
                </button>
            </a>
            <a href='https://open.spotify.com/artist/5xNgRWT0JdD8oagSush4yZ'>
                <button
                    type="button"
                    className="spotify"
                    aria-label="SpotifyLogo"
                >
                    <Spotify />
                </button>
            </a>

        </div>


    );
}

export default Socials;